.form__button {
    padding: 0;
    margin: 0;
    margin-top: 45px;
    margin-bottom: 40px;
    height: 50px;
    background: rgba(0, 0, 0, 1);
    border-radius: 2px;
    outline: none;
    border: none;
    font-family: 'Inter', Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    color: #fff;
    transition: background-color 0.2s linear;
    cursor: pointer;
    pointer-events: auto;
  }
  
  .form__button:hover {
    background: rgba(0, 0, 0, .8);
  }